import React, { useContext, useEffect, useState } from "react";
import { SandboxContext } from "../context";
import Figure from "../Figure";

const Result = () => {
  const {
    sandboxState: { colors, selectedColor, mode, figure },
    boxes,
    setBoxes,
  } = useContext(SandboxContext);
  const initPreview = { event: null, position: null };
  const [preview, setPreview] = useState(initPreview);
  const initDelete = { event: null, idx: null };
  const [toDelete, setToDelete] = useState(initDelete);

  useEffect(() => {
    if (mode === "delete") {
      setPreview(initPreview);
    }
    if (mode === "create") {
      setToDelete(initDelete);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mode]);

  const deleteBox = e => {
    if (mode !== "delete") return;
    e.stopPropagation();
    if (toDelete?.idx !== null) {
      setBoxes(oldValue => {
        const newValue = oldValue.filter((item, idx) => {
          return idx !== toDelete.idx;
        });
        return newValue;
      });
      setToDelete(initDelete);
    }
  };
  const createFigure = e => {
    if (mode !== "create") return;
    e.stopPropagation();
    if (preview?.position) {
      setBoxes(oldValue => {
        return [
          ...oldValue,
          { position: preview.position, color: colors[selectedColor], figure },
        ];
      });
      setPreview(initPreview);
    }
  };
  const setPreviewItem = (e, position) => {
    if (mode !== "create") return;
    if (
      !boxes.some(
        box =>
          box.position[0] === position?.[0] &&
          box.position[1] === position?.[1] &&
          box.position[2] === position?.[2]
      )
    )
      setPreview({ event: e, position });
    return e.stopPropagation();
  };
  const setDeleteItem = (e, idx) => {
    if (mode !== "delete") return;
    setToDelete({ event: e, idx });
    return e.stopPropagation();
  };
  return (
    <>
      {boxes.map(({ position, color, figure }, idx) => {
        return (
          <Figure
            figure={figure}
            key={position.join(";")}
            onHoverGroup={setDeleteItem}
            onHoverMesh={setPreviewItem}
            position={position}
            idx={idx}
            toDelete={toDelete.idx === idx}
            onDelete={deleteBox}
            color={color}
            previewItem={preview}
          />
        );
      })}
      {preview?.position ? (
        <Figure
          figure={figure}
          position={preview.position}
          preview
          onCreate={createFigure}
          color={colors[selectedColor]}
        />
      ) : null}
    </>
  );
};

export default Result;
