import React from "react";
import { useState } from "react";
import ColorPicket from "../ColorPicker";
import styled from "styled-components";
import PipetteSvg from "../PipetteIcon";

const StyledColors = styled.div`
  width: 50px;
  height: 50px;
  background: ${props => props.color};
  border-radius: 10px;
  border: ${({ selected }) => (selected ? "2px solid white" : "none")};
`;

const StyledColorInput = styled.div`
  position: relative;
`;

const ColorInput = ({ item, idx, setColor, selected, selectColor }) => {
  const [popover, setPopover] = useState(false);
  return (
    <StyledColorInput>
      <StyledColors
        selected={selected}
        key={item}
        onClick={() => selectColor(idx)}
        color={item}
      />
      <PipetteSvg onClick={() => setPopover(true)} />
      {popover ? (
        <ColorPicket
          color={item}
          setColor={setColor}
          idx={idx}
          onClose={() => setPopover(false)}
        />
      ) : null}
    </StyledColorInput>
  );
};

export default ColorInput;
