import { useEffect } from "react";
import styled from "styled-components";
import { SandboxContext } from "../context";
import { useSandboxState } from "../hooks";
import Menu from "../Menu";
import Sandbox from "../Sandbox";
import TopButtons from "../TopButtons";

const StyledApp = styled.div`
  background-color: #0c192c;
  display: flex;
  height: 100vh;
  width: 100vw;
  position: relative;
`;

function App() {
  const sandboxState = useSandboxState();

  useEffect(() => {
    const handleKey = e => {
      if (e.keyCode === 49) {
        sandboxState.nextMode();
      } else if (e.keyCode === 50) {
        sandboxState.nextFigure();
      } else if (e.keyCode === 51) {
        sandboxState.nextColor();
      }
    };
    document.addEventListener("keypress", handleKey);
    return () => {
      document.removeEventListener("keypress", handleKey);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <SandboxContext.Provider value={sandboxState}>
      <StyledApp>
        <Menu />
        <Sandbox />
        <TopButtons />
      </StyledApp>
    </SandboxContext.Provider>
  );
}

export default App;
