import React from "react";

const Figure = ({
  position,
  preview,
  onHoverMesh,
  onCreate,
  toDelete,
  onHoverGroup,
  idx,
  onDelete,
  color = "#ffa500",
  figure,
}) => {
  const size = 0.5;
  const opacity = preview ? 0.7 : 1;
  if (toDelete) color = "red";

  const setPreview = (e, position) => {
    if (!onHoverMesh) return;
    let posIdx = 0;
    let delta = 1 - Math.abs(e.face.normal.x);
    const dY = 1 - Math.abs(e.face.normal.y);
    if (dY <= delta) {
      delta = dY;
      posIdx = 1;
    }
    const dZ = 1 - Math.abs(e.face.normal.z);
    if (dZ <= delta) {
      delta = dZ;
      posIdx = 2;
    }

    const newPosition = position && [
      position[0] + (posIdx === 0 ? Math.round(e.face.normal.x) * size : 0),
      position[1] + (posIdx === 1 ? Math.round(e.face.normal.y) * size : 0),
      position[2] + (posIdx === 2 ? Math.round(e.face.normal.z) * size : 0),
    ];
    onHoverMesh(e, newPosition);
  };

  const setDelete = (e, idx) => {
    if (!onHoverGroup) return;
    onHoverGroup(e, idx);
  };

  const handleClick = e => {
    if (preview && onCreate) return onCreate(e);
    if (!preview && toDelete) return onDelete(e);
  };

  return (
    <group
      position={position}
      onClick={handleClick}
      onPointerMove={e => setDelete(e, idx)}
      onPointerLeave={e => setDelete(e, null)}
    >
      {figure === "cylinder" ? (
        <mesh
          position={[0, 0, 0]}
          onPointerMove={e => setPreview(e, position)}
          onPointerLeave={e => setPreview(e, null)}
        >
          <cylinderGeometry
            args={[(size - 0.05) / 2, (size - 0.05) / 2, size, 32]}
          />
          <meshStandardMaterial
            color={color}
            transparent={!!preview}
            opacity={opacity}
          />
        </mesh>
      ) : null}

      {figure === "cube" ? (
        <mesh
          position={[0, 0, 0]}
          onPointerMove={e => setPreview(e, position)}
          onPointerLeave={e => setPreview(e, null)}
        >
          <boxGeometry args={[size, size, size]} />
          <meshStandardMaterial
            color={color}
            transparent={!!preview}
            opacity={opacity}
          />
        </mesh>
      ) : null}

      {/* шапка */}
      <mesh position={[0, size / 2 + size / 10, 0]}>
        <cylinderGeometry args={[size / 3, size / 3, size / 5, 16]} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
    </group>
  );
};

export default Figure;
