import React, { useContext, useRef } from "react";
import styled from "styled-components";
import { saveAs } from "file-saver";
import { SandboxContext } from "../context";

const StyledButtons = styled.div`
  position: absolute;
  display: flex;
  gap: 5px;
  right: 0;
`;

const TopButtons = () => {
  const { getJSONState, loadState } = useContext(SandboxContext);
  const saveState = () => {
    const blob = new Blob([getJSONState()]);
    saveAs(blob, "SandboxSave.json");
  };
  const ref = useRef(null);
  const loadFile = () => {
    ref.current.click();
  };
  const handleFile = e => {
    const file = ref.current.files[0];
    const reader = new FileReader();
    reader.readAsText(file);
    reader.onload = function (evt) {
      loadState(evt.target.result);
    };
  };
  return (
    <StyledButtons>
      <button onClick={saveState}>Сохранить</button>
      <button onClick={loadFile}>Загрузить</button>
      <input
        style={{ display: "none" }}
        ref={ref}
        onChange={e => handleFile(e)}
        type="file"
      />
    </StyledButtons>
  );
};

export default TopButtons;
