import React, { useContext } from "react";
import { SandboxContext } from "../context";
import styled from "styled-components";
import ColorInput from "../ColorInput";

const StyledModeButton = styled.div`
  padding: 5px;
  border: 1px solid white;
  width: 70px;
  text-align: center;
  cursor: pointer;
  ${({ active }) => {
    return active
      ? `background: white;
    color: black;`
      : "";
  }}
`;

const StyledColorButton = styled.button`
  cursor: pointer;
  background: none;
  border: none;
  color: white;
  font-size: 20px;
`;

const StyledMenu = styled.div`
  display: flex;
  flex-direction: column;
  padding: 5px 20px;
  box-sizing: border-box;
  color: white;
  align-items: center;
  justify-content: flex-start;
  gap: 5px;
`;
const StyledLabel = styled.p`
  align-self: flex-start;
  margin-top: ${({ marginTop }) => (marginTop ? "10px" : "0px")};
`;

const Menu = () => {
  const { sandboxState, setMode, setColor, addColor, selectColor, setFigure } =
    useContext(SandboxContext);
  const colors = sandboxState.colors.map((item, idx) => {
    return (
      <ColorInput
        key={idx}
        idx={idx}
        item={item}
        setColor={setColor}
        selected={sandboxState.selectedColor === idx}
        selectColor={selectColor}
      />
    );
  });
  return (
    <StyledMenu>
      <StyledLabel>Режим:</StyledLabel>
      <StyledModeButton
        onClick={() => setMode("create")}
        active={sandboxState.mode === "create"}
      >
        Создание
      </StyledModeButton>
      <StyledModeButton
        onClick={() => setMode("delete")}
        active={sandboxState.mode === "delete"}
      >
        Удаление
      </StyledModeButton>
      <StyledLabel marginTop>Фигура:</StyledLabel>
      <StyledModeButton
        onClick={() => setFigure("cube")}
        active={sandboxState.figure === "cube"}
      >
        Куб
      </StyledModeButton>
      <StyledModeButton
        onClick={() => setFigure("cylinder")}
        active={sandboxState.figure === "cylinder"}
      >
        Цилиндр
      </StyledModeButton>
      <StyledLabel marginTop>Палитра:</StyledLabel>
      {colors}
      <StyledColorButton onClick={addColor}>+</StyledColorButton>
    </StyledMenu>
  );
};

export default Menu;
